import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { currentDate, EditForm, EditViewProps, formatDate, getSelectedItem, InputGroupInline, InputType, isEmpty, WindowComponent } from 'summer';
import { CLASS_NAME, ConfigMulta } from '../../../../models/dtos/mlf/configMulta';

interface MultaEditViewProps extends EditViewProps<ConfigMulta> {}

const MultaEditView: React.FC<MultaEditViewProps & RouteComponentProps> = props => {
  const current = currentDate();
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<ConfigMulta>({ mode: 'onBlur' });

  const defaultWidth: number = 110;

  useEffect(
    () => {
      let resetObject: ConfigMulta = selectedItem;
      if (isEmpty(selectedItem.id)) {
        resetObject = {
          ...selectedItem,
          dtInclusao: formatDate(current),
        };
      }
      formProps.reset(resetObject);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent title="Manutenção de Multa" wikiId={1009}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              id="dtInclusao"
              label="Data de Inclusão"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe a Data de Inclusão da Multa"
              type={InputType.DATE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, min: 0.01, max: 100, maxLength: 5 } }}
              id="taxa"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="Taxa díaria"
              labelWidth={defaultWidth}
              placeholder="Informe a Taxa díaria da Multa"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, min: 0.01, max: 100, maxLength: 5 } }}
              id="limite"
              isPrependLabel={true}
              label="Limite"
              labelWidth={defaultWidth}
              placeholder="Informe o Limite da Multa"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default MultaEditView;
